@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.skill{
    width: 200px;
    @include columnFlex(none);
    text-align: center;
    box-sizing: border-box;
    padding: 20px;
    @media only screen and (max-width: 750px) {
        margin-bottom: 20px;
        width: 120px;
    }
    h3{
        color: $headerReverseTextColor;
    }
    .icon{
        @include columnFlex(center);
        width: 60px;
        height: 60px;
        font-size: 45px;
        color: $primaryColor;
    }
    img{
        width: 40px;
        height: 40px;
    }
}
